var user = {
    username: "",
    toDoList: [[],[],[],[]]
}
var currentUser = {
    username: "",
};
var loggedIn = false;
export function fetchToDoList() {
    //returns a list with 4 lists that contain strings
    //return [[],[],[],[]];
    return user.toDoList;
}


export function updateToDoList(list) {
    user.toDoList = list;
    
    //returns nothing
}

export function isLoggedIn() {
    return loggedIn;
}
export function login(username, password) {
    currentUser.username = username;
    //return boolean
    if ((currentUser.username === user.username)){
        loggedIn = true;
        return true;
    }
    return false;

}

